<template>
  <v-stepper tile v-model="e1" class="mb-2">
    <v-stepper-header >
      <v-stepper-step :complete="e1 > 1" step="1"> </v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step :complete="e1 > 2" step="2"> </v-stepper-step>
      <v-divider></v-divider>
      <v-stepper-step step="3"> </v-stepper-step>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content step="1">
        <v-card class="mb-2">
          <v-layout class="pt-2">
            <v-flex xs12>
              <v-text-field
              ref="ad_title"
              v-model="ad_title"
              :rules="ad_title_rules"
              :eroor-messages="errorMessages"
              label=""
              outlined
              clearable
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs6 md6 >
              <v-text-field
              :class="pr"
              ref="ad_honorarium"
              v-model="ad_honorarium"
              :rules="ad_honorarium_rules"
              :eroor-messages="errorMessages"
              label=""
              :disabled="!ad_swich"
              clearable
              outlined
              ></v-text-field>
            </v-flex>
            <v-flex xs6 md6 class="d-flex pl-12">
                <v-switch class="pa-1  mt-3"
                    v-model="ad_swich"
                    :label="adSwichString"
                  ></v-switch>
            </v-flex>
          </v-layout>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field
              :class="pr"
              ref="ad_pone"
              v-model="ad_pone"
              :rules="ad_pone_rules"
              :eroor-messages="errorMessages"
              label=""
              outlined
              clearable
              @blur="adPoneChange"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 md6>
            <v-dialog
              ref="dialog"
              v-model="calender"
              :return-value.sync="ad_date"
              persistent
              width="290px"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  :class="pl"
                  ref="ad_date"
                  v-model="ad_date"
                  label=" "
                  readonly
                  outlined
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker v-model="ad_date" scrollable locale="ko">
                <v-spacer></v-spacer>
                <v-btn text color="primary" @click="calender = false">Cancel</v-btn>
                <v-btn text color="primary" @click="$refs.dialog.save(ad_date)">OK</v-btn>
              </v-date-picker>
            </v-dialog>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12>
              <v-text-field
                  ref="ad_place"
                  v-model="ad_place"
                  :rules="ad_place_rules"
                  :eroor-messages="errorMessages"
                  label=" "
                  outlined
                  readonly
                  clearable
                  @click="test"
                  >
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12>
              <v-textarea
              ref="ad_detail"
              v-model="ad_detail"
              label="    ."
              :rules="ad_detail_rules"
              :eroor-messages="errorMessages"
              outlined>
              </v-textarea>
            </v-flex>
          </v-layout>
        </v-card>
        <v-btn color="primary" @click="step1"></v-btn>
      </v-stepper-content>
      <v-stepper-content step="2">
        <v-card class="mb-12" color="grey lighten-1" height="200px"></v-card>
        <!-- <v-btn color="primary" @click="e1 ++ "></v-btn> -->
        <v-btn color="primary" @click="test"></v-btn>
    <v-btn text @click="e1 --"></v-btn>
      </v-stepper-content>
      <v-stepper-content step="3">
        <v-card class="mb-12" color="grey lighten-1" height="200px"></v-card>
        <!-- <v-btn color="primary" @click="e1 ++ "></v-btn> -->
        <v-btn color="primary" @click="test"></v-btn>
    <v-btn text @click="e1 --"></v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
        <!-- <KakaoMap ref="kakaoMap"/> -->
</template>

<script>
  import KakaoMap from '../KakaoMap'
  export default {
    data: () => ({
      ad_title:null, //
      ad_honorarium:null, //
      ad_swich : false, // 
      ad_pone:null, // 
      ad_detail:null, //
      ad_date:new Date().toISOString().substr(0, 10),// 
      ad_place : null,
      ad_place_rules : [
        v => !!v || "  ."
      ],
      errorMessages:null, 
      ad_title_rules:[ //  
        v => v === null ? '  ' 
        : v.length > 32 ? '32  .' 
        : v.replace(/\s/g, '').length !== 0 || " ."
      ],
      ad_honorarium_rules:[ // 
        v => !!v || '  .',
        v => Number.isInteger(v*1) || ' .'
      ],
      ad_pone_rules:[ //  
        v => v === null ? '  .'
        : v.length < 15 || '  .'
      ],
      ad_detail_rules:[//  
        v => v === null ? ' .' 
        : v.replace(/\s/g, '').length !== 0 || " ."
      ],
      e1:1, // 
      calender: false, // on/off 
      formHasErrors: false,//   
    }),
    mounted () {
    },
    components: {
      KakaoMap,
    },
    methods: {
      //      
      // 1        
      //   ,   
      step1(){
        this.formHasErrors = false;

        Object.keys(this.form).forEach(f => {
          if(!this.form[f] || this.$refs[f].hasError){
            this.formHasErrors = true;
            this.$refs[f].validate(true);
          }
        })

        if(this.ad_swich){
          if(!this.ad_ad_honorarium || this.$refs["ad_honorarium"].hasError){
            this.formHasErrors = true;
            this.$refs["ad_honorarium"].validate(true);
          }
        }

        if(!this.formHasErrors){
          this.e1 ++;
        }

      },
      test1(){
        this.ab_dialog = true;
        console.log(this.$refs);
      },
      test(){
        alert("!");
      },
      //   blur  
      //          
      adPoneChange(){
        if(!this.$refs["ad_pone"].hasError){
          let extraction = this.ad_pone.replace(/[^0-9]/g,'');
          let format_pone = ''; 
          let pone_bl = false;
          let regular = '';
          if(extraction.length === 11){ //11
            format_pone = extraction.replace(/(\d{3})(\d{4})(\d{4})/, '$1-$2-$3');
            regular = /^\d{3}-\d{3,4}-\d{4}$/;
          }else if(extraction.length === 8){ //8
            format_pone = extraction.replace(/(\d{4})(\d{4})/, '$1-$2');
            regular = /^\d{4}-\d{4}$/;
          }else if(extraction.length === 12 && extraction.indexOf('05') === 0){
            format_pone = extraction.replace(/(\d{4})(\d{4})(\d{4})/, '$1-$2-$3');
            regular = /^\d{4}-\d{4}-\d{4}$/;
          }else{
            if(extraction.indexOf('02') === 0){
              format_pone = extraction.length < 10 ?  extraction.replace(/(\d{2})(\d{3})(\d{4})/, '$1-$2-$3') :  extraction.replace(/(\d{2})(\d{4})(\d{4})/, '$1-$2-$3');
              regular = /^\d{2}-\d{3,4}-\d{4}$/;
            }else{
              format_pone = extraction.replace(/(\d{3})(\d{3})(\d{4})/, '$1-$2-$3');
              regular = /^\d{3}-\d{3,4}-\d{4}$/;
            }
          }
          pone_bl = regular.test(format_pone);
          if(!pone_bl){
            this.$refs["ad_pone"].errorBucket.push(" .");
            return false;
          }
          this.ad_pone = format_pone;
        }
      }
    },
    computed: {
      //    
      adSwichString(){
        return this.ad_swich ? '( : )' : '';
      },
      //  ,  
      form(){
        return{
          ad_title : this.ad_title,
          ad_pone : this.ad_pone,
          ad_date : this.ad_date,
          ad_detail : this.ad_detail,
        }
      },
      //   breakpoint   
      pr(){
        return this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm ? "pr-0" : "pr-1"
      },
      pl(){
        return this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm ? "pl-0" : "pl-1"
      }
      
    }
  }
</script>