<template>
<v-container>
    <v-btn
        color="primary"
        class="ma-2"
        dark
        @click="openMap"
      >
        Open Dialog 1
      </v-btn>
      <v-btn @click="test">
        <v-icon>{{cartgory_icon[testtest]}}</v-icon>
      </v-btn>
       <v-dialog
        v-model="map_dialog"
        v-resize="windowHeight"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
        scrollable
        eager
      >
        <v-card tile>
          <div id="map_header">
            <v-toolbar flat style="border: 1vw solid #1E88E5;" height='auto'>
              <span style="padding-right:16px;">
                <!-- <v-btn color="#1E88E5" dark @click="map_dialog = false" height="36"> -->
                <v-btn color="#1E88E5" dark @click="backward" height="36">
                  <v-icon v-if="map_header_switch">mdi-close</v-icon>
                  <v-icon v-else>mdi-arrow-left</v-icon>
                </v-btn>
              </span>
              <v-text-field 
              v-model="keyword"
              height="36" hide-details="auto" 
              prepend-inner-icon="mdi-map-marker"
              label=",  "
              single-line
              clearable
              @focus="searchFocus"
              @keyup="getList"
              ></v-text-field>
              <v-btn icon color="#1E88E5">
                <v-icon>mdi-magnify</v-icon>
              </v-btn>
            </v-toolbar>  
          </div>
          <div>
            <!-- /   -->
            <div id="map_switch_button" style="position:absolute; z-index:10; margin:1%; display:block">
              <v-btn @click="contentChange">{{map_button_name}}</v-btn>
            </div>
            <!--  -->
            <div id="map" style="width:100%;height:350px; display:block">
            </div>
            <div id="map_search_list" style="display:none;">
              <v-list>
                <v-list-item-group v-model="map_list">
                  <v-list-item>{{keyword}}</v-list-item>
                  <v-list-item v-for="bar in keyword_relevants" :key="bar" @click="test">
                      {{bar}}
                  </v-list-item>
                  <div id="pagination"></div>
                </v-list-item-group>
              </v-list>
            </div>
            <div id="map_ps_list"  style="display:none;">
              <v-divider></v-divider>
                 
            </div>
          </div>
        </v-card>
              
      </v-dialog>
</v-container>
</template>


<script>
// import Kmap from '../components/KakaoMap.vue'
export default {
  data: () => ({
    map_dialog:false,
    keyword: null, // 
    keyword_relevants:[], //  
    keyword_Guard:null, //      
    map_header_switch:false,
    map_switch:false,
    map_button_name:'',
    map_ps_list:null, //    
    map_search_list:null, //   
    map: null,
    map_list:null, // 
    map_place:null, //  
    map_container:null, // 
    map_option:null,
    map_relayout_bl:false,
    map_maerkers:[],
    map_ps:null,
    map_infownidow:null,
    cartgory_icon : {
      "" : 'mdi-map-marker',
      MT1 : 'mdi-cart',// 
      CS2 : 'mdi-store-24-hour',//
      PS3 : 'mdi-baby-face',//, 
      SC4 : 'mdi-school',//
      AC5 : 'mdi-teach',//
      PK6 : 'mdi-parking',//
      OL7 : 'mdi-gas-station',//, 
      SW8 : 'mdi-train',//
      BK9 : 'mdi-bank',//
      CT1 : 'mdi-movie-open',//
      AG2 : 'mdi-home-currency-usd',//
      PO3 : 'mdi-account-check',//
      AT4 : 'mdi-map-check',//
      AD5 : 'mdi-bed',//
      FD6 : 'mdi-silverware-fork-knife',//
      CE7 : 'mdi-coffee',//
      HP8 : 'mdi-hospital-box',//
      PM9 : 'mdi-pill',//
    },
    testtest : '',
  }),
  components:{
    // Kmap
  },
  methods:{
    test(){
     
    },
    backward(){
      if(this.map_header_switch){
        this.map_search_list.style.display = 'none'; 
        this.map_ps_list.style.display = 'none';
        this.map_switch_button.style.display = 'block';      
        this.map_container.style.display = 'block';
        this.map_header_switch = false;
      }else{
        this.map_dialog = false;
      }
    },
    contentChange(){
      // 
      if(!this.map_switch){
        if(!this.map_place){
          alert("    .");
          return false;
        }
        this.map_container.style.display = 'none';
        this.map_ps_list.style.display = 'block';
        this.map_switch = true;
      }else{
        this.map_ps_list.style.display = 'none';
        this.map_container.style.display = 'block';
        this.map_switch = false;
      }
    },
    searchFocus(){
      this.map_header_switch = true;
      this.map_switch_button.style.display = 'none';
      this.map_container.style.display = "none";
      this.map_search_list.style.display = "block";
    },
    getList(){
      if(!!this.keyword && this.keyword_Guard !== this.keyword){
      // if(!!this.keyword){
        console.log(this.keyword)
        this.keyword_Guard = this.keyword;
        this.$http.get('api/relevant/map?keyword='+ this.keyword).then(
          (res) => {
            console.log(res.data);
            this.keyword_relevants = res.data;
         }
        ).catch(err => {
          alert(err);
        })
        // this.getRelevant();
      }else{
        this.keyword_relevants = [];
      }
    },
    //   
    getRelevant(){
      this.$http.get('api/relevant/map?keyword='+ this.keyword).then(
        (res) => {
          console.log(res.data);
          this.keyword_relevants = res.data;
        }
      ).catch(err => {
        alert(err);
      })

     
    },
    openMap(){
      this.map_dialog = true;
      this.createMap();
    }, 
    createMap(){
      this.map_container = document.getElementById("map");
      this.map_search_list = document.getElementById("map_search_list");
      this.map_ps_list = document.getElementById("map_ps_list");
      this.map_switch_button = document.getElementById("map_switch_button");

      this.map_option = {
        center: new kakao.maps.LatLng(33.450701, 126.570667), //  
        level: 3 //   
      }
      this.map= new kakao.maps.Map(this.map_container, this.map_option);
      this.map_ps = new kakao.maps.services.Places();
      this.map_infownidow = new kakao.maps.InfoWindow({zIndex:1});

    
    },
    windowHeight() {
      let height = window.innerHeight - document.getElementById("map_header").offsetHeight;
      console.log(" !!")
      document.getElementById("map").style.height = height + 'px';
    },
    //     
    //   placeSearchCB  
    searchPlaces(){
      if(!this.keyword){
        console.log("!!");
         return false; 
      }
      if(!this.keyword.replace(/^\s+|\s+$/g, '')){
        console.log("  !");
        return false;
      }
      this.map_ps.keywordSearch(this.keyword, this.placeSearchCB);
    },
    //     
    //       
    placeSearchCB(data, status, pagination){
      if(status === kakao.maps.services.Status.OK) {
        // this.displayPlaces(data);

        // console.log("!!")
        console.log(data);

        this.map_list = data;
        this.displayPagination(pagination);
      }else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        // alert('   .');
        console.log(" ");
        this.map_list = null;
        return;

      }else if (status === kakao.maps.services.Status.ERROR) {
        console.log("  ");
        return;
      }
    },
    displayPlaces(places){
      console.log(places);
    },
    displayPagination(pagination){
        var paginationEl = document.getElementById('pagination'),
        fragment = document.createDocumentFragment(),
        i; 

    //    
    while (paginationEl.hasChildNodes()) {
        paginationEl.removeChild (paginationEl.lastChild);
    }

    for (i=1; i<=pagination.last; i++) {
        var el = document.createElement('a');
        el.href = "#";
        el.innerHTML = i;

        if (i===pagination.current) {
            el.className = 'on';
        } else {
            el.onclick = (function(i) {
                return function() {
                    pagination.gotoPage(i);
                }
            })(i);
        }

        fragment.appendChild(el);
    }
    paginationEl.appendChild(fragment);
    }
  },
  mounted(){
    
  },
  updated(){
    if(!this.map_relayout_bl){
      // let height = window.innerHeight - document.getElementById("map_header").offsetHeight;
      // console.log(" !!");
      // document.getElementById("map").style.height = height + 'px';
      console.log("!")
      this.windowHeight();
      this.map.relayout();
      this.map_relayout_bl = true;
    }
    
    // if(!this.map_relayout_bl){
    //   this.map.relayout();
    //   this.map_relayout_bl = true;
    //   console.log("   ")
    // }else{
    //   this.map_relayout_bl = false;
    //   console.log("    ")
    // }
  },
  
  
    
}
//    title 
//title     
</script>